  const header = headerPane.value?.$el
  const chart = chartPane.value?.$el

  if (src === "header") {
    chart.scrollTop = header.scrollTop
  }
  if (src === "chart") {
    header.scrollTop = chart.scrollTop
  }
}

const syncSize = () => {
  const header = headerOverlay.value
  const chart = chartOverlay.value
  if (header !== undefined) {
